﻿/// <reference path="../../main">

// ----------------------------- //
// ------- CTA Card Item ------- //
// ----------------------------- //

.#{$prefix}cta-item {
    background-color: $white;
    border-radius: $border-radius;
    width: calc(25% - 1.125rem);
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;
    max-width: calc(50% - 0.75rem);
    box-shadow: 0 2px 5px rgba($black, 0.1);
    position: relative;

    @include media($medium-media-query-size) {
        width: calc(50% - #{$gap-sm});
    }

    @include media(small) {
        width: 100%;
        max-width: 100%;
    }

    & + & {
        margin-left: 1.5rem;

        @include media(small) {
            margin-left: 0;
        }
    }

    &-img {
        position: relative;
        padding-bottom: 56.25%;

        img {
            position: absolute;
            object-fit: cover;
            object-position: center top;
            width: 100%;
            height: 100%;
            border-top-left-radius: $border-radius;
            border-top-right-radius: $border-radius;
        }
    }

    &-content {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 1.25rem;
        color: $primary-dark;

        @include media(small) {
            padding: $gap-sm;
        }

        .rich-text {
            @include font-size($small-font-sizes);

            @include media($medium-media-query-size) {
                font-size: 0.875rem;
            }
        }

        &-link {
            margin-top: 1.25rem;
            @include font-size($small-font-sizes);
            flex: 1 0 auto;
            display: flex;
            align-items: flex-end;

            @include media(medium) {
                margin-top: $space;
            }

            p {
                transition: $transition;
                color: $primary-medium;
                text-decoration: none;
                border-bottom: 1px solid $primary-medium;
                display: inline-block;
            }
        }
    }

    &-date {
        @include font-size($small-font-sizes);
        font-weight: $font-weight-bold;
        margin-bottom: $space;

        @include media(medium) {
            margin-bottom: 0.25rem;
        }

        @include media(small) {
            margin-bottom: 0;
        }

        & + .rich-text {
            margin-top: $gap-sm;

            @include media($medium-media-query-size) {
                margin-top: 0.8125rem;
            }
        }
    }

    &-heading {
        font-size: 2rem;
        color: $primary-medium;
        margin-bottom: $gap-sm;
        color: $primary-dark;

        @include media($medium-media-query-size) {
            font-size: 1.25rem;
            margin-bottom: 0.8125rem;
        }

        @include media(small) {
            font-size: 1.125rem;
        }
    }

    &-link {
        border-bottom: none !important;
        height: 100%;
        transition: all 0.2s ease-in-out;
        border-radius: $border-radius;
        display: flex;
        flex-direction: column;

        &:hover,
        &:focus {
            top: -0.25rem;
            box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);

            .#{$prefix}cta-item-content-link p {
                border-color: transparent;
            }
        }
    }
}



