﻿/// <reference path="../../main">

// ---------------------------- //
// ------- Social Media ------- //
// ---------------------------- //

.social-media {
    display: flex;

    &-container {
        margin: $gap-sm 0;
    }

    li {
        margin-right: 1.0625rem;

        a {
            border-bottom: none;

            &:hover {
                color: $primary-dark;
            }
        }

        svg {
            font-size: 1.8125rem;

            &.svg-linkedin {
                font-size: 1.4375rem;
                margin-top: 0.1875rem;
            }
        }
    }

    @media print {
        display: block;

        li {
            margin: $space 0;
        }

        a:after {
            content: ' (' attr(href) ') ';
            font-size: 0.8rem;
            font-weight: normal;
        }
    }
}


