﻿/// <reference path="../../main">


%themes {
    &.#{$prefix}theme {
        &-light, &-default {
            background: $gray-light;
            color: $primary-dark;

            .btn {
                @extend %btn-primary;
            }
        }

        &-dark {
            background: $primary-dark;
            color: $primary-light;

            .btn {
                @extend %btn-secondary;
            }
        }
    }
}




