﻿/// <reference path="../../main">

$fwcta-padding-map: ( null: 5rem, medium: 2.75rem, small: 2rem );

// STRUCTURE
.#{$prefix}fwcta {
    @include responsive-property('padding-top', $fwcta-padding-map);
    @include responsive-property('padding-bottom', $fwcta-padding-map);
    @extend %standard-padding-h;

    &-inner {
        max-width: 47.5rem;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        margin-bottom: 0.625rem;
    }

    &-content {
        margin-top: 1.25rem;

        @include media-range(small, medium) {
            margin-top: $gap-sm;
        }
    }

    &-cta {
        margin-top: $gap;

        a {
            @include media-min(small) {
                min-width: 14.5rem;
            }
        }
    }
}
// STYLING
.#{$prefix}fwcta {
    position: relative;

    &-img {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        display: block;
        right: 0;
        overflow: hidden;

        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: $primary-medium;
            opacity: 0.9;
            z-index: 1;
        }

        & + .adage-fwcta-inner {
            color: $white;

            p {
                color: $white !important;
            }

            .adage-fwcta-cta a {
                @extend %btn-secondary;
            }
        }

        picture {
            // For IE
            overflow: visible !important;
        }

        img {
            position: relative;
            object-fit: cover;
            object-position: center top;
            width: 100%;
            height: 100%;
        }
    }

    &-inner {
        position: relative;
        z-index: 2;
    }

    &-heading {
        @include font-size($large-font-sizes);

        @include media-range(small, medium) {
            margin-bottom: 0;
        }

        @include media(medium) {
            font-size: 1.5rem;
        }
    }

    &-content {
        font-size: 1.125rem;
    }
}

// THEMES
.#{$prefix}fwcta {
    @extend %themes;
}

.#{$prefix}theme-light {
    .#{$prefix}fwcta-content p {
        color: $primary-dark;
    }
}


