﻿/// <reference path="../../main">

// ------------------------------- //
// ------- General Content ------- //
// ------------------------------- //

$secondary-hero-color: darken($primary-medium, 4.125%) !default;

.#{$prefix}general {
    &-inner {
        @extend %standard-padding-h;
        padding-top: 2.875rem;
        max-width: 70rem;
        margin: 0 auto;

        @include media(medium) {
            padding: 2.25rem 2.75rem 0 2.75rem;
        }

        @include media(small) {
            padding: 1.5rem $gap-sm 0 $gap-sm;
        }
    }

    &-breadcrumbs {
        @extend %width-constraint;
        border-bottom: 1px solid $secondary-medium;
        padding-top: 1.125rem;
        padding-bottom: 1.125rem;
    }

    &-title {
        margin-bottom: 2rem;

        @include media(small) {
            margin-bottom: 1.5rem;
        }
    }

    & > .#{$prefix}block:not(.#{$prefix}block-full-bleed) {
        @extend %standard-margin-v;

        &:not(.#{$prefix}block-gen-wide) {
            max-width: 70rem;
            margin-left: auto;
            margin-right: auto;

            @include media(medium) {
                padding-left: 2.75rem;
                padding-right: 2.75rem;
            }

            @include media(small) {
                padding-left: $gap-sm;
                padding-right: $gap-sm;
            }
        }
    }

    .#{$prefix}video-block-container .#{$prefix}image-video-heading {
        padding-left: 0;
        padding-right: 0;
    }
}

%hero-tint {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.#{$prefix}secondary-hero {
    position: relative;
    display: flex;
    align-items: flex-end;
    overflow: hidden;

    &-no-img-tint {
        .#{$prefix}secondary-hero-img {
            padding-bottom: 0;
        }

        &:after {
            @extend %hero-tint;
            background-color: $secondary-hero-color;
        }
    }

    &-img {
        width: 100%;
        padding-bottom: 28.125%;
        align-self: flex-start;
        flex: 1 0;
        // For IE
        position: static !important;

        @include media(medium) {
            padding-bottom: 42.1875%;
        }

        picture {
            // For IE
            position: static !important;
        }

        img {
            position: absolute;
            object-fit: cover;
            object-position: center top;
            // For IE
            min-width: 100% !important;
            min-height: 100% !important;
            height: auto !important;
        }

        &-tint:after {
            @extend %hero-tint;
            background-color: rgba($secondary-hero-color, 0.65);
        }
    }

    &-inner {
        position: relative;
        margin: 2.5rem 0;
        width: 100%;
        z-index: 5;

        @include media(small) {
            margin: 1.325rem 0;
        }
    }

    &-heading {
        color: $white;
        margin-bottom: 0;

        & + .#{$prefix}hero-content {
            margin-top: 2.5rem;

            @include media(medium) {
                margin-top: $gap-sm;
            }

            @include media(medium) {
                margin-top: 1.25rem;
            }
        }

        &-no-tint {
            color: $secondary-hero-color;
        }
    }

    &-content {
        font-size: 1.125rem;

        &-container {
            @extend %width-constraint;
        }

        &-inner {
            color: $white;
            max-width: 45rem;

            @include media(medium) {
                max-width: 100%;
            }

            &-no-tint {
                color: $secondary-hero-color;
                max-width: $max-container-width / 2;
                width: 50%;
                background-color: $white;
                border-radius: $border-radius;
                padding: 1.125rem 1.75rem;
            }
        }
    }
}


