﻿/// <reference path="../../main">

// ---------------------------------- //
// ------- Date Listing Block ------- //
// ---------------------------------- //

.#{$prefix}date-listing {
    max-width: 60rem;
    margin-bottom: $gap;

    .#{$prefix}block-heading {
        margin-bottom: 1.75rem;

        @include media(medium) {
            margin-bottom: 2.25rem;
        }

        @include media(small) {
            margin-bottom: $gap-sm;
        }
    }

    &-item {
        display: flex;
        flex-direction: row-reverse;
        justify-content: flex-end;

        & + & {
            margin-top: 2rem;

            @include media(small) {
                margin-top: 1.125rem;
            }
        }

        &-date {
            border-right: 1px solid rgba($secondary-medium, 0.6);
            margin: 0 2rem 0 0;
            padding-right: 1.75rem;
            @include font-size($medium-font-sizes);
            flex: 0 0 auto;
            font-weight: $font-weight-normal;

            @include media(medium) {
                font-size: 1.5rem;
            }

            @include media(small) {
                font-size: 1.25rem;
                margin: 0 0.75rem 0 0;
                padding-right: 0.75rem;
            }

            span {
                display: block;
                text-transform: uppercase;
                @include font-size($reg-font-sizes);
                margin-top: 0.3125rem;

                @include media(medium) {
                    margin-top: 0.125rem;
                }

                @include media(small) {
                    margin-top: 0;
                }
            }
        }

        &-details {
            h3 {
                margin-bottom: 0;

                a {
                    @include link-helper($primary-medium);
                    font-size: 1.5rem;

                    @include media(small) {
                        font-size: 1.25rem;
                    }
                }
            }

            p {
                @include font-size($small-font-sizes);
                margin-top: 0.625rem;

                @include media(medium) {
                    font-size: 0.875rem;
                }
            }
        }
    }

    .btn {
        margin-top: 2rem;

        @include media(small) {
            margin-top: 1.5rem;
        }
    }
}



