﻿/// <reference path="../../main">

// ---------------------- //
// ------- Footer ------- //
// ---------------------- //

$footer-gray: #c4c4c4;

.footer {
    // Top
    &-top {
        border-top: 1px solid $footer-gray;
        background-color: $white;

        @media print {
            border: 0;
            background: none;
        }

        &-inner {
            @extend %width-constraint;
            display: flex;
            padding-top: 2.25rem;
            padding-bottom: 2.75rem;

            @include media(medium) {
                padding-top: 1.875rem;
                padding-bottom: $gap;
            }

            @include media(small) {
                padding-top: 2rem;
            }

            a {
                @include link-helper($primary-medium);
                font-size: 1rem;
            }

            p {
                font-size: 1rem;
            }
        }

        &-title {
            @include font-size($medium-font-sizes);
            margin-bottom: 0;
        }

        .social-media-container {
            margin: 1.5rem 0 0;

            @include media-min(small) {
                margin: 2rem 0 0;
            }
        }
    }
}
// Top accordions
.footer-top-inner .#{$prefix}accordion {
    &-inner {
        width: 100%;

        @include media-min(small) {
            display: flex;
            flex-wrap: wrap;
        }

        & > li {
            border-bottom: none;
            width: 25%;
            padding-right: 2rem;

            @include media(medium) {
                width: 50%;
            }

            @include media-range(small, medium) {
                &:nth-child(n + 3) {
                    margin-top: 1.125rem;
                }
            }

            @include media(small) {
                width: 100%;
                padding-right: 0;
            }

            @media print {
                width: auto;
                padding: 0;
            }

            &:not(.#{$prefix}accordion-item) {
                .footer-top-title {
                    margin-bottom: 0.125rem;

                    @include media(medium) {
                        margin-bottom: 0.3125rem;
                    }
                }

                @include media(small) {
                    margin-top: 1.5rem;
                }
            }

            &.#{$prefix}accordion-item + .#{$prefix}accordion-item {
                @include media(small) {
                    margin-top: 1.5rem;
                }
            }

            &.footer-block-last {
                @include media-min(medium) {
                    margin-left: auto;
                }
            }
        }
    }

    &-item {
        @media print {
            display: none !important;
        }
    }

    &-trigger {
        @include media-min(small) {
            cursor: text;
        }

        &:hover {
            color: $black;
            opacity: 1;
        }

        @include media-min(small) {
            padding: 0;
            pointer-events: none;

            &:after {
                display: none;
            }
        }

        @include media(small) {
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    &-panel {
        @include media-min(small) {
            height: auto !important;
        }

        &-content {
            @include media(medium) {
                padding-top: 0.325rem;
            }

            @include media(small) {
                padding-bottom: 0.25rem;
            }

            & > p {
                &:first-of-type {
                    @include media(small) {
                        margin-top: 0;
                    }
                }

                &:not(:first-of-type) {
                    margin-top: 0.8125rem;

                    @include media(medium) {
                        margin-top: 0.625rem;
                    }
                }

                &:last-of-type {
                    @include media(small) {
                        margin-bottom: 0;
                    }
                }

                margin-bottom: 0.8125rem;

                @include media(medium) {
                    margin-top: 0.625rem;
                }
            }
        }
    }
}
// Bottom
.footer-bottom {
    background-color: $primary-dark;
    color: $white;
    @include font-size($small-font-sizes);

    @include media(medium) {
        font-size: 0.875rem;
    }

    @media print {
        background: none !important;

        * {
            color: $black !important;
            text-align: left;
        }
    }

    &-inner {
        @extend %width-constraint;
        display: flex;
        justify-content: space-between;
        padding-top: 0.875rem;
        padding-bottom: 0.875rem;

        @include media(medium) {
            flex-direction: column;
            text-align: center;
            padding-top: $gap-sm;
            padding-bottom: $gap-sm;
        }

        .copyright {
            @include media-min(medium) {
                margin-right: $gap;
            }
        }

        .user-info-links {
            display: flex;
            flex-wrap: wrap;

            @media print {
                display: none;
            }

            @include media(medium) {
                justify-content: center;
                margin-top: 0.625rem;
            }

            p {
                margin: 0;
                position: relative;

                &:not(:last-of-type) {
                    margin-right: $gap-sm;

                    &:after {
                        content: '';
                        display: inline;
                        height: 1rem;
                        border-left: 1px solid $white;
                        position: absolute;
                        top: 0.4375rem;
                        right: -#{$space};

                        @include media(medium) {
                            top: 0.25rem;
                        }

                        @include media(small) {
                            top: 0.375rem;
                        }
                    }
                }
            }

            a {
                @include link-helper($white);
                border-color: transparent;
            }
        }
    }
}


