﻿/// <reference path="../../main">

// offset to allow for spacing of focus borders to prevent cropping
$panel-offset: 0.5rem !default;

// STRUCTURE
.#{$prefix}accordion {
    max-width: $max-line-length;

    &-panel {
        position: relative;
        padding-left: $panel-offset;
        padding-right: $panel-offset;
        margin-left: -#{$panel-offset};
        margin-right: -#{$panel-offset};
        overflow-y: hidden;
        transition: $transition height;

        &[aria-hidden='true'] {
            height: 0;
        }
    }
}

// STYLES
.#{$prefix}accordion {
    margin-left: auto;
    margin-right: auto;

    &-inner {
        list-style-type: none;
        padding-left: 0;

        > li {
            padding-left: 0;

            &:before {
                display: none;
            }
        }
    }

    &-heading {
        margin-bottom: $gap-sm;
    }

    &-trigger {
        @extend %un-button;
        @include font-size($reg-font-sizes);
        display: block;
        width: 100%;
        padding-top: $gap-sm;
        padding-bottom: $gap-sm;
        font-family: $heading-font;
        font-weight: bold;
        color: $black;
    }

    &-item {
        border-bottom: 1px solid black;
    }

    &-panel {
        &-content {
            padding-top: $space;
            padding-bottom: $space;
        }
    }
}

// ARROW/INDICATOR
.#{$prefix}accordion {
    &-trigger {
        position: relative;
        padding-right: $gap;

        &:after {
            content: '';
            width: $ui-icon-size;
            height: $ui-icon-size;
            display: block;
            position: absolute;
            right: $gap-sm;
            top: 50%;
            transform: translateY(#{- (hypotenuse($ui-icon-size, $ui-icon-size) / 2)}) rotate(45deg);
            transition: $transition transform;
            border-color: $primary-medium;
            border-style: solid;
            border-width: 0 $ui-icon-thickness $ui-icon-thickness 0;
        }

        &[aria-expanded='true'] {
            &:after {
                transform: translateY(#{- (hypotenuse($ui-icon-size, $ui-icon-size) / 4)}) rotate(-135deg);
            }
        }
    }
}




