// ------------------------- //
// ------- Home Page ------- //
// ------------------------- //

.#{$prefix}hero {
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    overflow: hidden;

    @include media(medium) {
        flex-direction: column;
    }

    &-img {
        width: 100%;
        padding-bottom: 43.75%;
        align-self: flex-start;
        flex: 1 0;
        // For IE
        position: static !important;

        @include media(medium) {
            padding-bottom: 56.25%;
            // For IE
            position: relative !important;
        }

        picture {
            // For IE
            position: static !important;
        }

        img {
            position: absolute;
            object-fit: cover;
            object-position: center top;
            // For IE
            min-width: 100% !important;
            min-height: 100% !important;
            height: auto !important;
        }
    }

    &-inner {
        position: relative;
        margin: 2.5rem 0;
        width: 100%;

        @include media(medium) {
            margin: 0;
            width: 100%;
            background-color: $white;
        }
    }

    &-heading {
        color: $primary-dark;
        margin-bottom: 0;

        & + .#{$prefix}hero-content {
            margin-top: .75rem;
        }

        & + .#{$prefix}hero-cta-container {
            margin-top: 1.25rem;
        }
    }

    &-content {
        font-size: 1.125rem;

        & + .#{$prefix}hero-cta-container {
            margin-top: 1.25rem;
        }

        &-container {
            @extend %width-constraint;
        }

        &-inner {
            max-width: $max-container-width / 2;
            width: 50%;
            background-color: rgba($white, 0.8);
            padding: 1.125rem 1.75rem;
            border-radius: 6px;

            @include media(medium) {
                padding: 2.25rem 0;
                max-width: 100%;
                width: 100%;
            }

            @include media(small) {
                padding: 2rem 0;
            }

            h1 {
                font-size: 3rem;
                font-weight: $font-weight-normal;
            }
        }
    }

    &-left-align {
        .#{$prefix}hero-content-container {
            display: flex;
            justify-content: flex-start;
        }
    }

    &-right-align {
        .#{$prefix}hero-content-container {
            display: flex;
            justify-content: flex-end;

            @include media(medium) {
                justify-content: flex-start;
            }
        }
    }
}


