﻿/// <reference path="../../main">

$search-width: 15.125rem !default;

.header {
    background: $white;

    @include media-min(medium) {
        position: fixed;
        left: 0;
        right: 0;
        z-index: 20;
    }

    &.sticky-top {
        @include media-min(medium) {
            position: fixed;
            left: 0;
            right: 0;
            z-index: 20;
            transition: top 0.2s ease-in-out 0s;
            box-shadow: 0px 1px 12px rgba(0, 0, 0, 0.1);
        }
    }

    &-top {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        background: $white;
        z-index: 20;
        box-shadow: 0px 1px 12px rgba(0, 0, 0, 0.1);

        @include media-min(medium) {
            position: static;
            height: 5.75rem;
            padding-top: 0.5rem;
            padding-bottom: 0.5rem;
            z-index: initial;
            box-shadow: none;
            display: flex;
            align-items: center;
        }

        @media print {
            position: static !important;
            justify-content: center;
            box-shadow: none;
        }

        &-inner {
            @extend %width-constraint;
            height: 3.75rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
        }
    }

    &-logo {
        position: relative;
        display: inline-block;
        max-width: 8.125rem;
        text-decoration: none;
        border: 0;
        z-index: 5;

        img {
            max-height: 2.625rem;
        }

        @include media-min(medium) {
            max-width: 13rem;

            img {
                max-height: 3.875rem;
            }
        }
    }

    .mobile-nav {
        display: flex;
        justify-content: flex-end;
        align-items: center;

        @include media-min(medium) {
            display: none;
        }

        &-search-trigger {
            @extend %un-button;
            display: flex;
            width: 1.5rem;
            height: 1.5rem;
            font-size: 1.5rem;
            line-height: 1;
            justify-content: center;
            align-items: center;
            margin-right: 1.75rem;

            @include media-min(small) {
                margin-right: 2.5rem;
            }

            .icon-search {
                fill: $primary-medium;
                font-size: 1.1875rem;
            }
        }

        &-trigger {
            display: inline-block;
            position: relative;
            padding: 0;
            width: 1.5rem;
            height: 1.5rem;
            background: none;
            border: 0;
            outline: 0;
            cursor: pointer;
            //@extend .svg-icon;
            &:hover {
                border: none;
            }

            &-icon {
                position: absolute;
                left: 50%;
                top: 50%;
                width: 1.125rem;
                height: 1.125rem;
                display: flex;
                flex-direction: column;
                justify-content: center;
                transform: translate(-50%, -50%);

                > span {
                    display: block;
                    height: 2px;
                    width: 100%;
                    background-color: $primary-medium;
                    font-size: 0;
                    position: relative;
                    transition: all 0.3s ease-in;
                    transition: background-color $transition ease-in-out;

                    &:before,
                    &:after {
                        transition: background-color $transition ease-in-out, transform 0.3s ease 0s, top 0.3s ease 0.3s;
                        content: '';
                        display: block;
                        position: absolute;
                        height: 100%;
                        width: 100%;
                        background-color: $primary-medium;
                        transform: rotate(0);
                    }

                    &:before {
                        top: -5px;
                    }

                    &:after {
                        top: 5px;
                    }
                }
            }

            &-open {
                .mobile-nav-trigger-icon > span {
                    background-color: transparent;

                    &:before,
                    &:after {
                        top: 0;
                        transition: transform 0.3s ease 0.3s, top 0.3s ease 0s;
                    }

                    &:before {
                        transform: rotate(-45deg);
                    }

                    &:after {
                        transform: rotate(45deg);
                    }
                }
            }
        }
    }

    &-nav {
        position: relative;
        background: $white;
        box-shadow: 0 1px 12px rgba($black, 0.1);

        @media print {
            display: none;
        }

        @include media(medium) {
            position: fixed;
            top: 3.75rem;
            left: 0;
            right: 0;
            bottom: 0;
            max-height: 0;
            overflow: hidden;
            z-index: 20;
            transition: max-height 0.3s ease 0s;

            @at-root .mobile-nav-open & {
                max-height: 80rem;
            }
        }

        @include media-min(medium) {
            top: 0 !important;
        }

        &-container {
            @include media-min(medium) {
                position: relative;
            }
        }

        &-inner {
            max-height: 100%;
            overflow-y: auto;
        }

        &-primary {
            color: $white;
            background-position: 50% 50%;
            background-size: cover;
            z-index: 10;
            transform: translateZ(0);
            -webkit-transform: translateZ(0);

            nav {
                padding-top: 1rem;
                padding-bottom: 1rem;
                @extend %width-constraint;
                background-color: $primary-medium;
            }

            a {
                @include link-helper($white);
                display: inline-block;
                padding: 0.25rem;
                font-weight: $font-weight-normal;
                text-decoration: none;
                border: 0;
                font-size: 1.25rem;
                line-height: 1.75/1.25;
                height: 100%;

                &:hover {
                    @include media-min(medium) {
                        background-color: #388cd7;
                        text-decoration: none;
                    }
                }
            }

            ul {
                display: block;
                list-style-type: none;
                margin: 0;
                padding: 0;

                li {
                    padding: 0;
                    text-align: center;
                }
            }

            @include media-min(tablet) {
                nav {
                    padding-top: 2.5rem;
                    padding-bottom: 2.5rem;
                }

                a {
                    padding: 0.5rem;
                    font-size: 1.75rem;
                    line-height: 2.25/1.75;
                }
            }

            @include media-min(medium) {
                background-image: none !important;
                background-color: $primary-medium;

                nav {
                    padding-top: 0;
                    padding-bottom: 0;
                    background: $primary-medium;
                }

                a {
                    font-size: 1.25rem;
                    line-height: 1.75/1.25;
                }

                ul {
                    display: flex;
                    width: 100%;
                    justify-content: center;

                    li {
                        margin: 0;
                    }

                    a {
                        padding: 0.625rem 1.8125rem;
                    }
                }
            }
        }

        &-secondary {
            &-inner {
                @extend %width-constraint;
                // @media (max-width: 70em) {
                //     display: none;
                // }
                @include media-min(medium) {
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;

                    &:before {
                        content: '';
                        display: block;
                        width: 13rem + $gap-sm;
                        flex: 0 0 auto;
                    }

                    &:after {
                        content: '';
                        display: block;
                        width: $search-width + $gap-sm;
                        flex: 0 0 auto;
                    }
                }

                nav {
                    // For IE
                    width: 100%;
                }
            }

            ul {
                list-style-type: none;
                margin: 0;
                padding: 0.5rem 0;

                li {
                    padding: 0;
                    text-align: center;
                }

                a {
                    display: inline-block;
                    padding: 0.5rem;
                    font-size: 1rem !important;
                    line-height: 1.5;
                    text-decoration: none;
                    border: 0;
                }
            }

            @include media-min(medium) {
                position: absolute;
                width: 100%;
                top: -3.575rem;

                ul {
                    padding: 0;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: flex-end;

                    li {
                        display: block;
                        padding: 0.125rem 0.9063rem;
                        line-height: 1;
                    }

                    a {
                        padding: 0;
                        font-size: 0.875rem;
                        line-height: 1.25/0.875;
                    }
                }
            }
        }
    }

    &-search {
        @extend %width-constraint;

        label {
            @include visually-hidden;
        }

        @media print {
            display: none;
        }

        @include media(medium) {
            position: fixed;
            top: 3.75rem;
            left: 0;
            right: 0;
            max-height: 0;
            background: $primary-light;
            overflow: hidden;
            z-index: 20;
            transition: max-height 0.3s ease 0s;

            @at-root .mobile-nav-search-open & {
                max-height: 10rem;
            }
        }

        @include media-min(medium) {
            position: absolute;
            top: 0 !important;
            left: 0;
            right: 0;
            width: 100%;
        }

        &-inner {
            padding-top: 0.625rem;
            padding-bottom: 0.625rem;

            @include media-min(medium) {
                position: relative;
                padding: 0;

                form {
                    position: absolute;
                    width: $search-width;
                    top: -4.125rem;
                    right: 0;
                }
            }
        }

        .form-group {
            margin: 0;
            display: flex;

            input {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
            }

            button {
                padding-left: 2px;
                padding-right: 2px;
                height: 2.375rem;
                border-top-right-radius: $border-radius;
                border-bottom-right-radius: $border-radius;

                svg {
                    font-size: 1.1875rem;
                }
            }
        }

        input[type='search'],
        input[type='text'] {
            padding: 0.25rem $space;

            @include media(medium) {
                max-width: 100%;
            }

            &:focus {
                outline: 0;
                border-color: $primary-dark;
                box-shadow: 0 0 0 1px $primary-dark;
            }
        }

        .btn {
            display: flex;
            min-width: 2.5rem;
            font-size: 1.5rem;
            justify-content: center;
            align-items: center;
        }
    }
}



