﻿/// <reference path="../../main">

.#{$prefix}image-text {
    @extend %width-constraint;

    &-item {
        display: flex;
        flex-direction: column;

        @include media-min(tablet) {
            flex-direction: row;
        }

        + .#{$prefix}image-text-item {
            margin-top: $gap;
        }

        &-details {
            padding-left: 0;
            padding-top: $space;

            @include media-min(tablet) {
                @include responsive-property('padding-left', $gutter-map);
                padding-top: 0;
            }

            &-date {
                font-size: 1rem;
                font-weight: bold;
                margin: 0;
                margin-bottom: 0.25rem;
                display: block;

                @include media(medium) {
                    margin-bottom: $space;
                }

                @include media(small) {
                    margin-bottom: 0.325rem;
                }
            }

            h3 {
                margin-bottom: $space;
                color: $primary-medium;

                @include media(medium) {
                    font-size: 1.25rem;
                    margin-bottom: 0.625rem;
                }

                @include media(small) {
                    margin-bottom: $space;
                }

                > a {
                    @include link-helper($primary-medium);
                    text-decoration: none;
                }
            }

            .rich-text {
                font-size: 1rem;

                @include media(medium) {
                    font-size: 0.875rem;
                }
            }
        }

        &-cta {
            margin-top: 1.125rem;

            @include media-min(small) {
                margin-top: 1.325rem;
            }
        }

        &-image {
            width: 65%;
            flex-grow: 0;
            flex-shrink: 0;

            @include media-min(tablet) {
                width: 31%;
            }

            &-container {
                width: 100%;
                padding-bottom: 56.25%;
                overflow: hidden;
                position: relative;

                img {
                    position: absolute;
                    object-fit: cover;
                    object-position: center top;
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}


