﻿/// <reference path="../../main">

// CONTAINER STRUCTURE
.#{$prefix}listing {
    &-heading {
    }

    &-container {
        margin-top: $gap-sm;
        margin-bottom: $gap;
    }

    &-item {
        & + & {
            @include responsive-property('margin-top', $gutter-map);
        }
    }

    &-cta {
    }
}

// ITEM STYLING -- overall
.#{$prefix}listing {
    &-heading {
        @include media-min(small) {
            margin-bottom: 3rem;
        }
    }

    &-item {
        &-content {
            @include media(tablet) {
                margin-top: $gap-sm;
            }
        }
    }
}

// ITEM STRUCTURE -- using flexbox for IE
.#{$prefix}listing {
    &-item {
        @include media-min(tablet) {
            display: flex;

            &-image {
                width: 0;
                flex: 1 1 auto;
            }

            &-content {
                flex: 1 1 auto;
            }

            &-image + &-content {
                @include responsive-property('margin-left', $gutter-map);
            }
        }

        &-content {
            @each $breakpoint, $value in $gutter-map {
                @if $breakpoint == null {
                    width: $value * 2;
                    flex-grow: 3;
                }
                @else {
                    @if $breakpoint == large {
                        @include media($breakpoint) {
                            width: $value;
                            flex-grow: 2;
                        }
                    }
                    @else {
                        @include media($breakpoint) {
                            width: 0;
                            flex-grow: 1;
                        }
                    }
                }
            }

            @include media(tablet) {
                width: auto;
            }
        }
    }
}

// ITEM STRUCTURE -- using grid with flexbox stuff being undone
.#{$prefix}listing {
    &-item {
        @supports(display: grid) {
            @include responsive-property('grid-gap', $gutter-map);

            @include media-min(tablet) {
                display: grid;

                &-image, &-content {
                    width: auto;
                }

                &-image + &-content {
                    margin-left: 0;
                }
            }

            @include media-range(tablet, medium) {
                grid-template-columns: repeat(2, 1fr);
            }

            @include media-range(medium, large) {
                grid-template-columns: repeat(3, 1fr);

                &-content {
                    grid-column: span 2;
                }
            }

            @include media-min(large) {
                grid-template-columns: repeat(4, 1fr);

                &-content {
                    grid-column: span 3;
                }
            }
        }
    }
}

// IMAGE RATIO
.#{$prefix}listing {
    &-item-image {
        &-inner {
            .#{$prefix}image-block {
                width: 100%;
                height: 0;
                position: relative;
                overflow: hidden;

                & > a {
                    width: 100%;
                    height: 100%;
                }

                img {
                    @extend %full-bleed-image;
                }

                [data-full-bleed-image-ie] {
                    @extend %full-bleed-image-ie-replacement;
                }
            }

            &.ratio-standard {
                .#{$prefix}image-block, .#{$prefix}video-block {
                    padding-bottom: ratioToPercent(4, 3);
                }
            }

            &.ratio-wide {
                .#{$prefix}image-block, .#{$prefix}video-block {
                    padding-bottom: ratioToPercent(16, 9);
                }
            }
        }
    }
}



