﻿/// <reference path="../../main">

// STRUCTURE/LAYOUT
.#{$prefix}gallery {

    &-heading {
        @extend %standard-padding-h;
    }

    &-container {
        @include responsive-property('padding-left', $giant-padding-map);
        @include responsive-property('padding-right', $giant-padding-map);
        margin-top: $gap-sm;
        margin-bottom: $gap;
    }

    &-item {
        &-caption {

            @include media-min(medium) {
                display: flex;
                margin-top: $gap-sm;

                &-details {
                    flex: 0 1 auto;
                    width: calc(75% - #{$gap});
                    padding-right: $gap;
                    order: -1;
                }

                &-page {
                    flex: 1 0 auto;
                    text-align: right;
                }
            }

            @include media(medium) {

                &-details {
                    margin-top: $gap-sm;
                }

                &-page {
                    text-align: center;
                }
            }

            @include media(small) {
                margin-top: $space;

                &-details {
                    margin-top: $space;
                }
            }
        }
    }
}


// STYLING
.#{$prefix}gallery {
    &-item {
        position: relative;

        &:before { // cover on top of inactive elements
            content: '';
            @extend %full-cover;
            // match to background of block, if not $primary-light
            background: rgba($primary-light, .75);
            transition: $transition;
            pointer-events: none;
            opacity: 0;
            z-index: 1;
        }

        &:not(.swiper-slide-active) {
            &:before {
                opacity: 1;
            }
        }

        &-caption {
            @include font-size($small-font-sizes);
            color: $secondary-medium;
        }
    }
}

// ARROWS
.#{$prefix}gallery {
    &-btn {
        &-next, &-prev {
            position: absolute;
            top: 50%;
            z-index: 10;
            margin-top: -$button-size / 2;
            color: $primary-light;
            background-color: $primary-medium;
            border: 0;
            cursor: pointer;
            transition: $transition;
            // tweak padding to alter arrow size
            padding: .9rem;
            @include responsive-property('width', $square-icon-size-map);
            @include responsive-property('height', $square-icon-size-map);

            svg {
                width: 100%;
                height: 100%;
                // using currentColor here would be preferable, but it causes a transition delay so we have to explicitly define the color
                fill: $primary-light;
                transition: $transition;

                &:after {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                }
            }

            &:focus, &:hover, &:active {
                background: $primary-light;
                color: $primary-medium;
                box-shadow: $shadow;

                svg {
                    fill: $primary-medium;
                }
            }

            @include media(small) {
                margin-top: -$button-size;
                padding: .6rem;
            }
        }

        &-next {
            right: 0;
        }

        &-prev {
            left: 0;
        }
    }
}



