﻿/// <reference path="../../main">

// ---------------------------------- //
// ------- CTA Card Container ------- //
// ---------------------------------- //

.#{$prefix}cta {
    &-inner {
        @extend %width-constraint;
        @extend %standard-padding-v;
    }

    &-heading {
        color: $black;
        margin-bottom: 2rem;

        @include media($medium-media-query-size) {
            font-size: 1.75rem;
            margin-bottom: 1.625rem;
        }

        @include media(small) {
            font-size: 1.5rem;
            margin-bottom: 1.25rem;
        }
    }

    &-content {
        display: flex;
        flex-wrap: wrap;

        @include media-min(small) {
            margin-bottom: -1.5rem;
        }

        & > .#{$prefix}cta-item:last-of-type {
            @include media(small) {
                margin-bottom: 0;
            }
        }

        & > .#{$prefix}cta-item:nth-child(4n + 1) {
            margin-left: 0;
        }

        @include media-range(small, $medium-media-query-size) {
            & > .#{$prefix}cta-item:nth-child(2n + 1) {
                margin-left: 0;
            }
        }

        &-three {
            & > .#{$prefix}cta-item {
                @include media-min($medium-media-query-size) {
                    &:nth-child(n + 5) {
                        max-width: calc(25% - 1.125rem);
                    }
                }

                @include media-range(small, $medium-media-query-size) {
                    max-width: 100%;

                    & + .#{$prefix}cta-item {
                        margin-left: 0;
                    }

                    &:first-of-type {
                        width: 100%;
                    }

                    &:not(:first-of-type):nth-child(odd) {
                        margin-left: 1.5rem;
                    }
                }

                .#{$prefix}cta-item-img + .#{$prefix}cta-item-content > .#{$prefix}cta-item-heading {
                    @extend .#{$prefix}cta-item-heading;
                }
            }
        }
    }

    &-btn {
        margin-top: 2.5rem;

        @include media($medium-media-query-size) {
            margin-top: 2rem;
        }

        @include media(small) {
            margin-top: 1.5rem;
        }
    }
}


